@import url(https://fonts.googleapis.com/css2?family=Work+Sans&display=swap);

.web-menu{
    display: flex;
    flex: 1 1;
    align-items: center;
}
.web-option{
    
    font-size: 20px;
    margin-right: 16px;
    font-weight: 700;
}
.web-option a {
    display: flex;
    text-decoration: none;
    color: black;
    align-items: center;
}
.option-icon{
    display: flex;
    align-items: center;
    margin-right: 4px;
}
.web-option:last-child{
    margin-right: 0px;
}
.mobile-view{
    background-color: white;
    height: 100vh;
    width: 60vw;
    position: absolute;
    right: 0px;
    top:-6px;
    padding:18px 12px;
    display: flex;
    flex-direction: column;
    
}
.close-icon{
    display: flex;
    justify-content: flex-end;
    font-size: 24px;
}
.mobile-design{
    display: flex;
    text-decoration: none;
    flex-direction: column;
    width: 100%;
    padding: 24px;
    color: black;
    align-items: center;
    justify-content: center;
    height: 100%;
}
.mobile-options{
    font-size: 24px;
    margin: 10px 0px;
    display: flex;
}
.mobile-options a {
    display: flex;
    text-decoration: none;
    color: black;
    align-items: center;
    
}
.option-icon{
    display: flex;
    align-items: center;
    margin-right: 4px;
}
.header{
    display: flex;
    justify-content: space-between;
    flex:1 1;
    padding: 12px;
    position: relative;
    align-items: baseline;
}
.menu{
    display: flex;
    flex: 1 1;
    justify-content: flex-end;
}
.menu-icon{
   font-size: 24px;
}
.mobile{
    display: none;
}
.web{
    display: flex;
}
.logo{
    font-size: 36px;
    color: blueviolet;
    font-weight: 800;
}

@media only screen and (max-width:720px){
    .mobile{
        display: flex;
    }
    .web{
        display: none;
    }
    .logo{
        font-size: 24px;
    }
}
.footer{
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 24px;
}
.social-contact{
    display: flex;
    justify-content: center;
    align-items: center;
}
.social-icon-div{
    padding:6px;
    cursor:pointer;
    margin:6px;
}
.social-icon{
    height: 60px;
}
@media only screen and(max-width:720px){
    .social-icon{
        height: 40px;
    }
}
.about{
    padding-bottom: 88px;
}
.about-top{
    display: flex;
    justify-content: space-between;
    align-items: center;
}
.pic{
    height: 420px;
}
.about-info{
    font-size: 24px;
}
.info-name{
    color: blueviolet;
    font-size: 30px;
}
@media only screen and (max-width:720px){
    .about-top{
        flex-direction: column;
        justify-content: center;
        align-items: center;
    }
    .about-info{
        font-size: 20px;
    }
    .pic{
        height: 240px;
    }
    .info-name{
        font-size: 24px;
    }
}
.project-card{
    flex:1 1;
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin:16px 0px;
    padding:8px 0px;
}
.project-image{
    display: flex;
    justify-content: center;
}
.project-photo{
    width:70%;
}
.project-info{
    margin-right: 24px;
}
.project-title{
    font-size: 20px;
    font-weight: 600;
}
.project-tabs{
    display: flex;
    max-width: 60%;
    flex-wrap: wrap;
}
.tab{
    padding:4px 8px;
    background-color: rgb(191, 253, 255);
    border-radius: 4px;
    margin-right: 4px;
    margin-bottom: 4px;
}
.project-links{
    display: flex;
    justify-content: flex-start;
    align-items: center;
    flex-wrap: wrap;
    
}
.project-link{
    display: flex;
    margin-right: 10px;
    justify-content: center;
    align-items: center;
    background-color: rgb(191, 253, 255);
    text-decoration: none;
    color: black;
    border-radius: 4px;
    
}
.link-button{
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 16px;
    width:-webkit-max-content;
    width:max-content;
    padding: 4px 8px;
    margin:6px;
}
.link-button i{
    display: flex;
    justify-content: center;
    align-items: center;
}
@media only screen and (max-width:720px){
    .project-photo{
        width:70%;
    }
    .project-card{
        flex-direction: column-reverse;
    }
    .project-info{
        margin-left: 0px;
        margin-top:12px;
    }
    .project-tabs{
        max-width: 100%;
    }
}


.seperator{
    background-image: linear-gradient(to right top, #d16ba5, #c777b9, #ba83ca, #aa8fd8, #9a9ae1, #8aa7ec, #79b3f4, #69bff8, #52cffe, #41dfff, #46eefa, #5ffbf1);
    height: 6px;
    width:40%;
    border-radius: 3px;
    margin:16px 0px
}
.project{
    display: flex;
    flex-direction: column;
    flex: 1 1;
}




.skills{
    display: flex;
    flex-direction: column;
    width:100%;
}
.skill-section{
    display:flex;
    justify-content:space-between;
    margin:12px 0px;
}
.skills-section-title{
    font-size:24px;
    font-weight: 600;
}
.skill-list{
    display:flex;
    flex-wrap:wrap;
    max-width:70%;
    justify-content: flex-end;
}

.skill-card{
    width:-webkit-max-content;
    width:max-content;
    padding:8px 16px;
    height:120px;
    display:flex;
    flex-direction:column;
    justify-content: center;
    align-items: center;
    margin:6px;
    border-radius:8px;
}
.skill-icon{
    font-size:80px;
}
.skill-label{
    font-size:18px;
}
@media only screen and(max-width:720px){
    .skill-card{
        width:-webkit-max-content;
        width:max-content;
        height:100px;
    }
    .skill-icon{
        font-size:60px
    }
    .skill-list{
        max-width:100%;
        justify-content: flex-end;
    }
    .skills-section{
        flex-direction: column;
        align-items:center;
    }
}
.work-card{
    padding: 8px;
    width:45%;
    margin-top:16px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    flex-direction: row;
}
.company-logo{
    height: 80px;
}
.work-info{
    display: flex;
    align-items: flex-start;
    flex-direction: column;
}
.work-dates{
    color: grey;
}
.work-desc{
    text-align: left;
    display: -webkit-box;
    -webkit-line-clamp: 2;
    -webkit-box-orient: horizontal;
    overflow: hidden;
    text-overflow: ellipsis;
    max-width: 80%;
}
@media only screen and (max-width:720px){
    .work-card{
        width:80%;
    }
}
.work{
    display: flex;
    flex-direction: column;
    width:100%
}
.work-list{
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    align-items: center;
    margin:24px 0px;
}
.contact{
    display: flex;
    flex-direction: column;
    flex: 1 1;
}
.section-title{
    font-size:32px;
    font-weight: 800;
}
.contact-container{
    display: flex;
    flex:1 1;
    justify-content: space-between;
    align-items: center;
    margin:16px 0px;
}
.contact-left{
    max-width:60%;
    font-size:20px;
    text-align: center;
}
.contact-right{
    background-color: aquamarine;
    padding: 8px 8px;
    font-size: 24px;
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 4px;
}
.contact-right a{
    text-decoration: none;
    color: black;
}
@media only screen and (max-width:720px){
    .contact-container{
        flex-direction: column;
        justify-content: center;
        align-items: center;
    }
    .contact-left{
        max-width: 100%;
        font-size: 16px;
        margin-bottom: 12px;
    }
    .contact-right{
        font-size: 16px;
    }
}
.body{
 padding: 12px;
 padding-bottom: 36px;
}
html{
    scroll-behavior: smooth;
}
.home{
    display: flex;
    flex-direction: column;
    width: 70%;
    margin: auto;
}
@media only screen and (max-width:1024px){
    .home{
        width:90%;
    }
}
@media only screen and (max-width:512px){
    .home{
        width:100%;
    }
}
body
{
  font-family: 'Work Sans', sans-serif;
}
